import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import { Tab, Nav, Navbar, Container } from 'react-bootstrap'
import logo from './img/logo.png'
import profile from './img/profile.png'
import ProfileComponent from './Components/ProfileComponent'
import SkillComponent from './Components/SkillComponent'
import ArticleComponent from './Components/ArticleComponent'



class App extends React.Component{
  render(){
    const arr_profesion = ['Student', 'Cyber Security Enthusiast', 'IT GEEK', 'Fullstack Developer'];
    const profesion = []
    for (const [index, value] of arr_profesion.entries()) {
      profesion.push(
          <div className="bg-customblue m-1 px-3 py-2" eventKey={index}>{value}</div>
        )
    }
    return(
    <>
    <div className="custom-bg">
      <Navbar className="ps-2">
        <Container>
          <Navbar.Brand href="#">
            <img src={logo} width="40px" alt=""/>
          </Navbar.Brand>
        </Container>
      </Navbar>
      
      <section className="py-5">
        <Container>
          <div className="text-center py-5">
            <img src={profile} width="200px" alt=""/>
              <h3 className="text-orange mt-3">Y A T C O D E</h3>
            <hr width="30px" className="text-darkblue mx-auto p-1 mb-3"/>
            <div className="pt-1 pb-4">{profesion}</div>
          </div>
        </Container>
      </section>
      </div>
      
      <Container className="pt-5 custom-bg">
        <Tab.Container id="left-tabs-example" defaultActiveKey="profile" className="pt-5">
          <div className="custom-width">
          <Nav variant="pills">
            <Nav.Item className="m-1 tab-menu">
              <Nav.Link eventKey="profile">PROFILE</Nav.Link>
            </Nav.Item>
            <Nav.Item className="m-1 tab-menu">
              <Nav.Link eventKey="skill">SKILL</Nav.Link>
            </Nav.Item>
            <Nav.Item className="m-1 tab-menu">
              <Nav.Link eventKey="article">ARTICLE</Nav.Link>
            </Nav.Item>
          </Nav>
          </div>
          
          <Tab.Content>
            <Tab.Pane eventKey="profile">
              <ProfileComponent />
            </Tab.Pane>
            <Tab.Pane eventKey="skill">
              <SkillComponent />
            </Tab.Pane>
            <Tab.Pane eventKey="article">
              <ArticleComponent />
            </Tab.Pane>
          </Tab.Content>
        </Tab.Container>
      </Container>
    </>
    )
  }
}


export default App;