import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Row, Col } from "react-bootstrap";

class SkillComponent extends React.Component {
  render() {
    const arr_prolang = ['python', 'javascript', 'bash', 'php', 'cplusplus', 'java'];
    const language = []
    for (const [index, value] of arr_prolang.entries()) {
      language.push(
          <div className="bg-customlight shadow-sm p-2 m-2">
          <img
            id={index}
            src={"https://cdn.jsdelivr.net/gh/devicons/devicon/icons/"+value+"/"+value+"-original.svg"} alt={value}
            width="33px"
          />
        </div>
        )
    }

    /*const doesntexist = ['laravel', 'tailwind', 'codeigniter']*/
    const arr_other = ['html5', 'css3', 'bootstrap', 'react', 'vuejs', 'jupyter', 'tensorflow', 'nodejs', 'git', 'django', 'express', 'mysql', 'mongodb', 'threejs', 'vscode'];
    const otherskill = []
    for (const [index, value] of arr_other.entries()) {
      otherskill.push(
          <div className="bg-customlight shadow-sm p-2 m-2">
          <img
            id={index}
            src={"https://cdn.jsdelivr.net/gh/devicons/devicon/icons/"+value+"/"+value+"-original.svg"} alt={value}
            width="33px"
          />
        </div>
        )
    }
    return (
      <div>
        <Row>
          <Col sm={8}>
            <div className="mt-5 heading-bg pt-2">
              <h4 className="text-orange">
                My <div className="heading-border">Profesion</div>
              </h4>
            </div>

            <Row className="mt-4">
              <Col className="my-2" sm>
                <div className="bg-customblue float-start">
                  <div className="icon bi bi-bug"></div>
                </div>
                <div id="my-profesion">
                  <div className="profesion-title">IT Security</div>
                  <div className="profesion-desc">2 Years Experience</div>
                </div>
              </Col>

              <Col className="my-2" sm>
                <div className="bg-customblue float-start">
                  <div className="icon bi bi-globe2"></div>
                </div>
                <div id="my-profesion">
                  <div className="profesion-title">Web Developer</div>
                  <div className="profesion-desc">2 Years Experience</div>
                </div>
              </Col>

              <Col className="my-2" sm>
                <div className="bg-customblue float-start">
                  <div className="icon bi bi-laptop"></div>
                </div>
                <div id="my-profesion">
                  <div className="profesion-title">Software Developer</div>
                  <div className="profesion-desc">2 Months Experience</div>
                </div>
              </Col>
            </Row>
          </Col>

          <Col sm={4} className="px-auto">
            <div className="mt-5 heading-bg pt-2">
              <h4 className="text-orange">
                Familiar Coding <div className="heading-border">With</div>
              </h4>
            </div>
            <div className="text-center">
            <div className="bg-customlight shadow-sm p-2 m-2 mt-4">
              <img
                src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/python/python-original.svg" alt=""
                width="33px"
              />
            </div>
            <div className="bg-customlight shadow-sm p-2 m-2 mt-4">
              <img
                src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/javascript/javascript-original.svg" alt=""
                width="33px"
              />
            </div>
            <div className="bg-customlight shadow-sm p-2 m-2 mt-4">
              <img
                src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/bash/bash-original.svg" alt=""
                width="33px"
              />
            </div>
            <div className="bg-customlight shadow-sm p-2 m-2 mt-4">
              <img
                src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/php/php-original.svg" alt=""
                width="33px"
              />
            </div>
            <div className="bg-customlight shadow-sm p-2 m-2 mt-4">
              <img
                src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/cplusplus/cplusplus-original.svg" alt=""
                width="33px"
              />
            </div>
            </div>
          </Col>
        </Row>

        <Row>
          <Col 
            className="text-center pt-5 px-3"
          sm>
            <div className="my-5 pt-2">
              <h4 className="text-orange">
                Coding <div className="heading-border">Language</div>
              </h4>
            </div>

            {language}
          </Col>
          <Col 
            className="text-center pt-5 px-3"
          sm>
            <div className="my-5 pt-2">
              <h4 className="text-orange">
                Framework, Library, <div className="heading-border">Etc</div>
              </h4>
            </div>

             {otherskill}
          </Col>
        </Row>

        <div className="my-5 pt-2">
          <h4 className="text-orange">
            Project<div className="heading-border">'s</div>
          </h4>
        </div>

        <Row className="mb-5">
          <Col sm={3}>
            <div className="bg-customlight px-3 pt-3">
              <p>Not Added For Now</p>
            </div>
          </Col>
        </Row>

      </div>
    );
  }
}

export default SkillComponent;
